import React, {Component, Fragment} from 'react'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import Homepage from './Homepage'
import NotFound from './NotFound'
import Login from './Login'
import Nav from './Nav'
import Leaderboard from './Leaderboard'
import {connect} from 'react-redux'
import {handleInitialData} from '../actions/shared.js'
import LoadingBar from 'react-redux-loading'
import QuestionDetails from './QuestionDetails'
import NewQuestion from './NewQuestion'
import '../css/App.css';

class App extends Component {
  componentDidMount() {
    this.props.dispatch(handleInitialData())
  }
  render() {
    console.log(window.location.href)
    console.log(window.location.href.indexOf('github'));
    const gitHubPage = window.location.href.indexOf('github') > -1? '/Would_you_rather' : ''
    console.log(gitHubPage);
    return (<Router>
      <Fragment>
        <LoadingBar/>

        <div className="App">
          {
            this.props.loading === true
              ? <Login/>
              : <Fragment>
                  <Nav/>
                  <Switch>
                    <Route path={gitHubPage+'/'} exact={true} component={Homepage}/>
                    <Route path={gitHubPage+'/leaderboard'} exact={true} component={Leaderboard}/>
                    <Route path={gitHubPage+'/question/:id'} component={QuestionDetails}/>
                    <Route path={gitHubPage+'/new'} component={NewQuestion}/>
                    <Route component={NotFound}/>
                  </Switch>
                </Fragment>
          }
        </div>
      </Fragment>
    </Router>);
  }
}

function mapStateToProps({authedUser}) {
  return {
    loading: authedUser === null
  }
}
export default connect(mapStateToProps)(App)
